"use strict";

var THREE        = require('three'),
    TWEEN        = require('tween.js'),
    projector    = require('three.js-projector'),
    renderer     = require('three-canvas-renderer');


//===============================
//      LANDING ANIMATION
//===============================

document.addEventListener('DOMContentLoaded', function() {

    var container;
    var camera, scene, renderer;

    var raycaster;
    var mouse;

    function init() {

        container = document.getElementById('particles');

        camera = new THREE.PerspectiveCamera( 70, window.innerWidth / window.innerHeight, 1, 10000 );
        camera.position.y = 300;
        camera.position.z = 500;

        scene = new THREE.Scene();

        var geometry = new THREE.BoxGeometry( 30, 30, 30 );

        function buildCube() {

            var colors = [
                0x231cc7,
                0xefefef,
                //0x004cd9,
                //0x2700d9,
                //0x5300d9,
                //0x7400d9,
                // 0xa200d9
            ];

            var randomNum = Math.floor(Math.random() * (2 - 0) + 0);

            var color = colors[randomNum];

            var object = new THREE.Mesh( geometry, new THREE.MeshBasicMaterial( {
            color: color, opacity: 0.9 }));
            object.position.x = Math.random() * 800 - 400;
            object.position.y = Math.random() * 800 - 400;
            object.position.z = Math.random() * 800 - 400;

            object.scale.x = 0.01 * 1 + 1;
            object.scale.y = 0.01 * 1 + 1;
            object.scale.z = 0.01 * 1 + 1;

            object.rotation.x = Math.random() * 2 * Math.PI;
            object.rotation.y = Math.random() * 2 * Math.PI;
            object.rotation.z = Math.random() * 2 * Math.PI;
            scene.add( object );

        }

        function setIntervalX(callback, delay, repetitions) {
            var x = 0;
            var intervalID = window.setInterval(function () {

               callback();

               if (++x === repetitions) {
                   window.clearInterval(intervalID);
               }
            }, delay);
        }

        // This will be repeated every for 5 times with 1 second intervals:
        setTimeout(function() {
            setIntervalX(function () {
                buildCube();
            }, 75, 25);
        }, 3350);

        raycaster = new THREE.Raycaster();
        mouse = new THREE.Vector2();

        renderer = new THREE.CanvasRenderer();
        renderer.setClearColor( 0xffffff );
        renderer.setPixelRatio( window.devicePixelRatio );
        renderer.setSize( window.innerWidth, window.innerHeight );
        container.appendChild(renderer.domElement);

        document.addEventListener( 'mousedown', onDocumentMouseDown, false );
        document.addEventListener( 'touchstart', onDocumentTouchStart, false );

        //

        window.addEventListener( 'resize', onWindowResize, false );

    }

    function getRandomColor() {
        var colors = [
            // 0x8A8FFC,
            // 0xFD8BB5,
            // 0xFEDB8E,
            // 0x92D4F5,
            // 0x75DF98,
            // 0xF3A273,
            // 0x688FE2,
            // 0xEB6E7B,
            // 0x60D0E3,
            // 0xD999DE,
            // 0xD0F187,
            // 0xFD98E2,
            // 0xBB99FC,
            // 0x6DEDBE,
            0x004cd9,
            0x2700d9,
            0x5300d9,
            0x7400d9,
            0xa200d9
        ];
        var randomNum = Math.floor(Math.random() * (5 - 0) + 0);
        var color = colors[randomNum];

        return color;
    }

    function onWindowResize() {

        camera.aspect = window.innerWidth / window.innerHeight;
        camera.updateProjectionMatrix();

        renderer.setSize( window.innerWidth, window.innerHeight );

    }

    function onDocumentTouchStart( event ) {

        event.preventDefault();

        event.clientX = event.touches[0].clientX;
        event.clientY = event.touches[0].clientY;
        onDocumentMouseDown( event );

    }

    function onDocumentMouseDown( event ) {

        event.preventDefault();

        mouse.x = ( event.clientX / renderer.domElement.clientWidth ) * 2 - 1;
        mouse.y = - ( event.clientY / renderer.domElement.clientHeight ) * 2 + 1;

        raycaster.setFromCamera( mouse, camera );

        var intersects = raycaster.intersectObjects( scene.children );

        if ( intersects.length > 0 ) {

            new TWEEN.Tween( intersects[ 0 ].object.position ).to( {
                x: Math.random() * 800 - 400,
                y: Math.random() * 800 - 400,
                z: Math.random() * 800 - 400 }, 2000 )
            .easing( TWEEN.Easing.Elastic.Out).start();

            new TWEEN.Tween( intersects[ 0 ].object.rotation ).to( {
                x: Math.random() * 2 * Math.PI,
                y: Math.random() * 2 * Math.PI,
                z: Math.random() * 2 * Math.PI }, 2000 )
            .easing( TWEEN.Easing.Elastic.Out).start();

            intersects[0].object.material.color.setHex( getRandomColor() );

        }

    }

    function animate() {

        requestAnimationFrame( animate );
        render();

    }

    var radius = 600;
    var theta = 0;

    function render() {

        TWEEN.update();

        theta += 0.1;

        camera.position.x = radius * Math.sin( THREE.Math.degToRad( theta ) );
        camera.position.y = radius * Math.sin( THREE.Math.degToRad( theta ) );
        camera.position.z = radius * Math.cos( THREE.Math.degToRad( theta ) );
        camera.lookAt( scene.position );

        renderer.render( scene, camera );

    }

    var animateOnResize = debounce(function() {
        var windowWidth = Math.max(document.documentElement.clientWidth, window.innerWidth || 0),
            particles   = document.getElementById('particles');

        if(windowWidth > 480 && !particles.hasChildNodes()) {
            init();
            animate();
        }

        if(windowWidth < 480 && particles.hasChildNodes()) {
            particles.removeChild(particles.firstChild);
        }

    }, 500);

    window.addEventListener('resize', animateOnResize);

    function callAnimation() {
        var windowWidth = Math.max(document.documentElement.clientWidth, window.innerWidth || 0);

        if(windowWidth > 480) {
            init();
            animate();
        }
    }
    callAnimation();

});



//===============================
//      MISC FUNCTIONALITY
//===============================

document.addEventListener('DOMContentLoaded', function() {

    var about = document.getElementById('about');
    about.classList.add('visible');

    var scrollAnimate = debounce(function() {
        var about   = document.getElementById('about'),
            work    = document.getElementById('work'),
            contact = document.getElementById('contact');

        var h = Math.max(document.documentElement.clientHeight, window.innerHeight || 0);

        var scroll   = window.pageYOffset + (h / 3),
            aboutTop = about.getBoundingClientRect().top,
            workTop  = work.getBoundingClientRect().top;

        if(scroll > aboutTop)
            about.classList.add('show-text');

        if(scroll < aboutTop)
            about.classList.remove('show-text');

        if(window.pageYOffset > workTop)
            contact.classList.add('visible');

        if(window.pageYOffset < workTop)
            contact.classList.remove('visible');


    }, 5);

    window.addEventListener('scroll', scrollAnimate);

});



//===============================
//      SMOOTH SCROLL
//===============================

document.addEventListener('DOMContentLoaded', function() {

    // Feature Test
    if ( 'querySelector' in document && 'addEventListener' in window && Array.prototype.forEach ) {

        // Function to animate the scroll
        var smoothScroll = function (anchor, duration, easing) {

            // Function to control easing
            var easingPattern = function (type, time) {
                return time < 0.5 ? 4 * time * time * time : (time - 1) * (2 * time - 2) * (2 * time - 2) + 1;
            };

            // Calculate how far and how fast to scroll
            // http://www.quirksmode.org/blog/archives/2008/01/using_the_assig.html
            var startLocation = window.pageYOffset;
            var endLocation = function (anchor) {
                var distance = 0;
                if (anchor.offsetParent) {
                    do {
                        distance += anchor.offsetTop;
                        anchor = anchor.offsetParent;
                    } while (anchor);
                }
                return distance + 25;
            };
            var distance = endLocation(anchor) - startLocation;
            var increments = distance / (duration / 16);
            var timeLapsed = 0;
            var percentage, position, stopAnimation;

            // Scroll the page by an increment, and check if it's time to stop
            var animateScroll = function () {
                timeLapsed += 16;
                percentage = ( timeLapsed / duration );
                percentage = ( percentage > 1 ) ? 1 : percentage;
                position = startLocation + ( distance * easingPattern(easing, percentage) );
                window.scrollTo(0, position);
                stopAnimation();
            };

            // Stop the animation
            if ( increments >= 0 ) { // If scrolling down
                // Stop animation when you reach the anchor OR the bottom of the page
                stopAnimation = function () {
                    var travelled = window.pageYOffset;
                    if ( (travelled >= (endLocation(anchor) - increments)) || ((window.innerHeight + travelled) >= document.body.scrollHeight) ) {
                        clearInterval(runAnimation);
                    }
                };
            } else { // If scrolling up
                // Stop animation when you reach the anchor OR the top of the page
                stopAnimation = function () {
                    var travelled = window.pageYOffset;
                    if ( travelled <= endLocation(anchor) || travelled <= 0 ) {
                        clearInterval(runAnimation);
                    }
                };
            }

            // Loop the animation function
            var runAnimation = setInterval(animateScroll, 16);

        };

        // For each smooth scroll link
        var scrollToggle = document.querySelectorAll('.smooth-scroll');
        [].forEach.call(scrollToggle, function(toggle) {

            // When the smooth scroll link is clicked
            toggle.addEventListener('click', function(e) {

                // Prevent the default link behavior
                e.preventDefault();

                // Get anchor link and calculate distance from the top
                var dataID = toggle.getAttribute('href');
                var dataTarget = document.querySelector(dataID);
                var dataSpeed = toggle.getAttribute('data-speed');
                var dataEasing = toggle.getAttribute('data-easing');

                // If the anchor exists
                if (dataTarget) {
                    // Scroll to the anchor
                    smoothScroll(dataTarget, dataSpeed || 500, dataEasing || 'easeInOutCubic');
                }

            }, false);

        });

    }

});

// Debounce function

function debounce(func, wait, immediate) {
	var timeout;
	return function() {
		var context = this, args = arguments;
		var later = function() {
			timeout = null;
			if (!immediate) func.apply(context, args);
		};
		var callNow = immediate && !timeout;
		clearTimeout(timeout);
		timeout = setTimeout(later, wait);
		if (callNow) func.apply(context, args);
	};
};
